export default {
	data() {
		return {
			sample:{
				id: '',
				name: '',
				price: 0,
				project: ''
			},
			samples:[]
		}
	},
	methods: {
		getSample() {
            for(let i=1;i< 100;i++){
				this.samples.push({id:i,name:'西红柿'+i,price:i,project:'农药残留'})
			}
		},
		setInputname(sample) {
			console.log(sample)
			uni.setStorage({
				key: 'data',
				data: {
					sample : sample,
					}
			})
			uni.navigateBack({
				url: 'addSample'
				
			})
		}
	}

}
